<template lang="en">
    <div>
        <dl v-for="lang in languages">
            <dt>{{ lang.language }}</dt>
            <dd>
                <div class="progress-bar">
                    <span class="fill" :style="{width: lang.percent + '%'}"></span>
                </div>
            </dd>
        </dl>
    </div>
</template>
<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      languages: []
    };
  },
  methods: {
    getLanguagesInfo() {
      Utilities.sendRequest("languages").then(langs => {
        this.languages = langs;
      });
    }
  },
  created() {
    this.getLanguagesInfo();
  }
};
</script>
<style lang="en"></style>
