<template lang="en">
    <div class="skills-container">
        <div class="individual-skill" v-for="skill in skills">
            <div class="tech">
                <p><strong>{{ skill.technologyName }}</strong></p>                
                <progressbar 
                    radius=65
                    v-bind:progress=skill.percent
                    stroke=7>
                </progressbar>
                <p>{{ skill.percent }}%</p>
            </div>

            <div class="frameworks" v-if="frameworks[skill.technologyName].length > 0">
                <p><strong>Frameworks: </strong></p>
                &nbsp;
                <ul>
                    <li v-for="framework in frameworks[skill.technologyName]">
                        <p>{{ framework.framework }} - {{ framework.percent }}%</p>
                    </li>
                </ul>
            </div>
            
            <hr class="hr">
        </div>  
    </div>
</template>
<script>
import { Utilities } from "@/assets/js/utilities.js";
import progressbar from "@/components/ProgressbarCircle.component";

export default {
  data() {
    return {
      skills: [],
      frameworks: [],
      apiRequested: false,
      progress: 0
    };
  },
  methods: {
    getTechSkillsInfo() {
      Utilities.sendRequest("tech-skills").then(response => {
        this.skills = response.techSkills;
        this.frameworks = response.frameworks;
        this.apiRequested = true;
      });
    }
  },
  created: function() {
    this.getTechSkillsInfo();
  },
  components: {
    progressbar
  }
};
</script>
<style lang="en"></style>
