<template lang="en">
     <ol class="skills-list">
        <li v-for="skill in skills" v-html="skill.description"></li>
    </ol>
</template>
<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      skills: []
    };
  },
  methods: {
    getSkills() {
      Utilities.sendRequest("skills").then(response => {
        this.skills = response;
      });
    }
  },
  created() {
    this.getSkills();
  }
};
</script>
<style lang="en"></style>
